.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 32px 16px 16px;
  border-radius: 5px;
  border: 1px solid #f3f3f3;
  background: #fff;
  width: 200px;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    display: block;
    height: 134px;
    width: 160px;
    margin-bottom: 40px;
  }

  &__name {
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: $main-text-color;
    margin-bottom: 4px;
  }

  &__code {
    color: $secondary-text-color;
    margin-bottom: 16px;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 24px;
  }

  &__title {
    font-size: 12px;
    line-height: 18px;
    color: $secondary-text-color;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  }

  &__cost {
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }

  &__button {
    text-transform: uppercase;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 166px;
    height: 40px;
    border-radius: 5px;
    color: #fff;
    background-color: $button-main-color;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;

    &:hover {
      background-color: #fff;
      color: $button-main-color;
      border: 1px solid $button-main-color;
    }
  }
}

.card:hover .card__name {
  color: #34568b;
}
